<template>
  <div class="produtividade">
    <div class="container">
      <div class="panel panel-sm">
        <div class="panel-heading"> 
          <h4>Calcular Pontuação</h4>
        </div>
        <div class="panel-body">
          <div class="form-group">
            <div class="col-sm-9">
              <input type="file" id="csv_file" name="csv_file" class="form-control" @change="loadCSV($event)">
            </div>
          </div>
			<label for="basic-url">Pontuação</label>
			<div class="input-group">
			  <div class="input-group-prepend">
				<span class="input-group-text" id="basic-addon3">Meta</span>
			  </div>
			  <input type="text" class="form-control" id="meta" aria-describedby="basic-addon3" value="90" v-model="meta">
			  <div class="input-group-prepend">
				<span class="input-group-text">Pontos</span>
			  </div>
			  <input type="text" class="form-control" aria-label="Amount (to the nearest dollar)" v-model="total">
			  <div class="input-group-append">
			  </div>
			  <div class="input-group-prepend">
				<span class="input-group-text">Diferença</span>
			  </div>
			  <input type="text" class="form-control" aria-describedby="basic-addon3" v-model="diferenca">
			</div>		  
		  <br/>
		  
		  
          <table v-if="filteredData">
            <thead>
              <tr>
                <th v-for="key in parse_header"
				  v-show="key!='Editar'"
                  @click="sortBy(key)"
                  :class="{ active: sortKey == key }">
                  {{ key | capitalize }}
                  <span class="arrow" :class="sortOrders[key] > 0 ? 'asc' : 'dsc'" v-show="sortKey == key && key != 'Pontos' && key != 'Computar'">
                  </span>
                </th>
              </tr>
            </thead> 
            <tr v-for="csv in filteredData">
              <td v-for="key in parse_header" v-show="key != 'Editar'">
			    <span v-show="key != 'Computar' && key != 'Pontos'">
                  {{csv[key]}}
				</span>
				<span style= "float:right; text-align:right; width:70px;" v-show="key == 'Pontos' && csv['Editar'] == 1">
                  {{csv[key]}}
				  <i class = "fas fa-pencil-alt" style="padding:5px;" @click="csv['Editar'] = 0"> </i>
				</span>  
				<span style= "float:right; text-align:right; width:70px;" v-show="key == 'Pontos' && csv['Editar'] == 0">
				  <input type="text"  style="width: 40px; text-align: right;" align="right" v-model="csv[key]">
				  <i class = "fas fa-save" style="padding:8px;" @click="csv['Editar'] = 1"> </i>
				</span>  
				<span v-show="key == 'Computar'">
				  <input type="checkbox" aria-label="Checkbox for following text input" v-model="csv[key]">
				</span>  
				
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>	
</template>


<script>
export default {
  name: 'Produtividade',
  props: {
    msg: String
  },
  data() {
	return {
	  channel_name: '',
	  channel_fields: [],
	  channel_entries: [],
	  parse_header: [],
	  filterKey: '',
	  parse_csv: [],
	  sortOrders:{},
	  sortKey: 'Última Atualização',
	  total: 0,
	  meta: 90,
	  diferenca: 0
	};
  },
  filters: {
	capitalize: function (str) {
	  return str.charAt(0).toUpperCase() + str.slice(1)
	}
  },
  
  computed: {
	filteredData: function () {
	  var sortKey = this.sortKey
	  var filterKey = this.filterKey && this.filterKey.toLowerCase()
	  var order = this.sortOrders[sortKey] || 1
	  var data = this.parse_csv
      let total2=0 
	  let total = data.reduce(function(total, item){
        if ((item.Pontos>0) && (item.Computar==1))
          total2 = total2 + Number(item.Pontos)
      },0)
	  this.total = total2.toFixed(2)
	  this.diferenca = this.meta - this.total
	  if (filterKey) {
		data = data.filter(function (row) {
		  return Object.keys(row).some(function (key) {
			return String(row[key]).toLowerCase().indexOf(filterKey) > -1
		  })
		})
	  }
	  if ((sortKey) && (sortKey != 'Computar')  && (sortKey != 'Pontos') && (sortKey != '')) {
		data = data.slice().sort(function (a, b) {
		  a = a[sortKey]
		  b = b[sortKey]
		  return (a === b ? 0 : a > b ? 1 : -1) * order
		})
	  }
	  return data
	}
  },

  methods: {
	sortBy: function (key) {
	  var vm = this
	  vm.sortKey = ''
	  vm.sortOrders[key] = vm.sortOrders[key] * -1
	  if ((key != 'Pontos') && (key != 'Computar'))
	    vm.sortKey = key
	   else	
	    vm.sortKey = 'Última Atualização'
//      this.$set(vm.sortOrders, key, vm.sortOrders[key] * -1);
//	  this.$forceUpdate();

	},
	csvJSON(csv){
	  var vm = this
	  var lines = csv.split("\n")
	  var result = []
	  var headers = lines[0].split(";")
	  var contador=0
		headers.splice(7,3)
		headers.splice(3,2)
		headers.push('Pontos')
		headers.push('Computar')
		headers.push('Editar')
		vm.parse_header=headers
	  
	  lines[0].split(";").forEach(function (key) {
		contador=contador+1;
		if (contador<8)		
		vm.sortOrders[key] = 1
	  })
	  
	  lines.map(function(line, indexLine){
		if (indexLine < 1) return // Jump header line
		
		var obj = {}
		var currentline = line.split(";")
		currentline.splice(7,3)
		currentline.splice(3,2)
		if (currentline[4]=='Concluída')
		{
			if (currentline[1]=='Aposentadoria por Idade Urbana')
			  currentline.push(1.00)
			if (currentline[1]=='Aposentadoria por Idade Rural') 
			  currentline.push(1.05)
			if (currentline[1]=='Salário-Maternidade Rural')
			  currentline.push(0.75)
			if (currentline[1]=='Salário-Maternidade Urbano')
			  currentline.push(0.75)
			if (currentline[1]=='Pensão por Morte Rural')
			  currentline.push(0.74)
			if (currentline[1]=='Pensão por Morte Urbana')
			  currentline.push(0.74)
			if (currentline[1]=='Aposentadoria por Tempo de Contribuição')
			  currentline.push(1.05)
			if (currentline[1]=='Aposentadoria da Pessoa com Deficiência por Idade')
			  currentline.push(0.79)
			if (currentline[1]=='Aposentadoria da Pessoa com Deficiência por Tempo de Contribuição')
			  currentline.push(0.96)
			if (currentline[1]=='Benefício Assistencial à Pessoa com Deficiência')
			  currentline.push(0.25)
			if (currentline[1]=='Auxílio-Reclusão Rural')
			  currentline.push(0.75)
			if (currentline[1]=='Benefício Assistencial ao Idoso')
			  currentline.push(1.05)
    	    currentline.push(1)
		}
         else		
		  if (currentline[4]=='Exigência')
		  {
	        currentline.push(0.20)
    	    currentline.push(1)
		  }	
          else
		  {
            currentline.push(0)
            currentline.push(0)
		  }	
   	    currentline.push(1)
	
	  	
		  
		headers.map(function(header, indexHeader){
		  obj[header] = currentline[indexHeader]
		})
		result.push(obj)
	  })
	  
	  result.pop() // remove the last item because undefined values
	  
	  
	  return result // JavaScript object
	},
	loadCSV(e) {
	  var vm = this
	  if (window.FileReader) {
		var reader = new FileReader();
		reader.readAsText(e.target.files[0], "ISO-8859-1");
		// Handle errors load
		reader.onload = function(event) {
		  var csv = event.target.result;
		  vm.parse_csv = vm.csvJSON(csv)
//		  console.log(vm.parse_csv);
		  
		};
		reader.onerror = function(evt) {
		  if(evt.target.error.name == "NotReadableError") {
			alert("Canno't read file !");
		  }
		};
	  } else {
		alert('FileReader are not supported in this browser.');
	  }
	}
  }


  
}
</script>

